import uniLoadMore from '../../components/uni-load-more.vue'
var _this;
export default {
	components: { //2注册组件
		uniLoadMore
	},
	data() {
		return {
			name: "",
			role: "",				
			starCount: 0,
			forksCount: 0,
			visitTotal: 0,
			// loadingText: '加载中...',
			// loadingType: 0, //定义加载方式 0---contentdown  1---contentrefresh 2---contentnomore
			// contentText: {
			// 	contentdown: '上拉显示更多',
			// 	contentrefresh: '正在加载...',
			// 	contentnomore: '没有更多数据了'
			// }
		};
	},
	mounted() {
		_this = this;
		this.initData();
		this.numDH(20)
	},
	onPullDownRefresh: function() {
		//刷新数据
		_this.initData();
		//下拉刷新的时候请求一次数据
		setTimeout(function() {
			uni.stopPullDownRefresh(); //得到数据后停止下拉刷新
		}, 1000);
	},
	// onReachBottom: function() {
	// 		_this.loadingType = 1;
	// 		uni.showNavigationBarLoading();//显示加载动画
	// 		setTimeout(function(){
	// 			_this.loadingType = 2;
	// 			uni.hideNavigationBarLoading();
	// 		},2000)
					
	// },
	methods: {
		initData(){ //初始化数据
		    this.$db.userToken(token=>{
				var _userInfo = _this.$db.get("userInfo");
				if(_userInfo){
					this.name = _userInfo.nickName?_userInfo.nickName:_userInfo.username
					this.role = _userInfo.role_id==2?'普通用户':'门店管理员'
				}
			})
			
		},
		navigatorChange(_url){
			this.$db.userToken(token=>{
				_this.$common.navigateTo(_url)
			})
		},
		coutNum(e) {
			if (e > 1000 && e < 10000) {
				e = (e / 1000).toFixed(1) + 'k'
			}
			if (e > 10000) {
				e = (e / 10000).toFixed(1) + 'W'
			}
			return e
		},
		numDH(i) {
			var that = this;
			if (i < 20) {
				setTimeout(function() {
					that.starCount = i
					that.forksCount = i
					that.visitTotal = i
					i++
					that.numDH(i);
				}, 20)
			} else {
				that.starCount = that.coutNum(3000),
				that.forksCount = that.coutNum(484),
				that.visitTotal = that.coutNum(24000)
			}
		}
	}
}
