var _this;
export default {
	data() {
		return {
			serchInfo: '',
			page_size: 10,
			current: 1,  //当前页面
			tasks:[],
			InputBottom: 0,
			TabCur: 0,
			navs: [
				{title: "推荐"},
				{title: "全部"},
				{title: "点工"},
				{title: "包工"}
			],
			isCard: true
		};
	},
	mounted() {
		_this = this;
		_this.refreshData(true)
	},
	methods: {
		refreshData(_refresh){
			if(_refresh){
				_this.current = 1
				_this.tasks = []
			}
			let data = {
				search: _this.serchInfo,
				page_size: _this.page_size,
				current: _this.current
			}
			this.$api.getTaskList(data,res=>{
				if(res.code == 0){
					var _newTasks = res.data.map(task=>{
						task.imgList = task.imgList.split(",")
						task.create_time = _this.$common.timeToAgoDate(task.create_time,604800)//7D
						return task
					})
					console.log(_newTasks)
					_this.tasks = _this.tasks.concat(_newTasks)
					
				}
			})
		},
		InputFocus(e) {
			this.InputBottom = e.detail.height
		},
		InputBlur(e) {
			this.InputBottom = 0
		},
		cancelSearch(){
			this.serchInfo = '';
		},
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
		}
	}
}
